@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:wght@500&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans', 'Helvetica', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
  font-family: 'Open Sans Condensed';
  padding: 20px 60px;
}

.menu-item{min-width:30%;height:380px;display:flex;flex-direction:column;flex:1 1 auto;align-items:center;justify-content:center;margin:0 7.5px 30px;overflow:hidden}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{opacity:0.9}.menu-item.large{height:460px}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-size:cover;background-position:center}.menu-item .content{display:flex;flex-direction:column;align-items:center;justify-content:center;opacity:1;position:relative}.menu-item .content .title{font-weight:bold;margin:10px 6px 0;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}.directory__heading{width:100%;margin-bottom:20px;margin-top:20px;font-family:Lora, serif;font-size:34px}

.homepage{margin-top:-20px;display:flex;flex-direction:column;align-items:center;padding:20px 80px}.hero{position:relative;width:100vw;height:60vh;margin-bottom:40px}.hero__content{display:flex;flex-direction:column;align-items:center;padding-top:100px;color:black;z-index:1;height:100%}.hero__content h1{font-size:40px;font-family:Lora, serif}.hero__content .hero__button{padding:10px 20px;background-color:black;color:white;font-size:16px;border:none;margin-top:20px;-webkit-animation-duration:0.5s ease;animation-duration:0.5s ease;box-shadow:3px 3px 51px -18px rgba(0,0,0,0.76);-webkit-box-shadow:3px 3px 51px -18px rgba(0,0,0,0.76);-moz-box-shadow:3px 3px 51px -18px rgba(0,0,0,0.76)}.hero__content .hero__button:focus{border:none;outline:0}.hero__content .hero__button:hover{background-color:#181818;box-shadow:none}.hero__image{object-fit:cover;overflow:hidden;position:absolute;top:0;left:0;right:0;bottom:0;z-index:-1}.hero__image img{width:100%}

.shop-page__hero{display:flex;align-items:center;height:300px;margin-bottom:50px;padding:20px 60px;width:100vw;margin-left:-60px}.shop-page__hero h1{color:white;font-size:56px}

.collection-preview{display:flex;flex-direction:column;margin-bottom:30px}.collection-preview .title{font-size:28px;margin-bottom:25px}.collection-preview .title a{text-decoration:none;color:#300b0b}.collection-preview .title a:after{content:' \2934 '}.collection-preview .preview{display:flex;justify-content:space-between}

.collection-item{width:22vw;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item:hover .image{opacity:0.8}.collection-item:hover .custom-button{opacity:0.85;display:flex}.collection-item .collection-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .collection-footer .price{width:10%}

.custom-button{min-width:165px;width:auto;height:50px;letter-spacing:0.5px;line-height:50px;padding:0 35px 0 35px;font-size:15px;background-color:black;color:white;text-transform:uppercase;font-family:'Open Sans Condensed';font-weight:bolder;border:none;cursor:pointer;display:flex;justify-content:center}.custom-button:hover{background-color:white;color:black;border:1px solid black}.custom-button.google-sign-in{background-color:#4285f4;color:white}.custom-button.google-sign-in:hover{background-color:#357ae8;border:none}.custom-button.inverted{background-color:white;color:black;border:1px solid black}.custom-button.inverted:hover{background-color:black;color:white;border:none}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}

.collection-page{display:flex;flex-direction:column}.collection-page .title{font-size:38px;margin:0 auto 30px}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:10px}.collection-page .items .collection-item{margin-bottom:30px}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:50px auto 0}.checkout-page .checkout-header{width:100%;padding:10px 0;display:flex;justify-content:space-between;border-bottom:1px solid darkgrey}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .total{margin-top:30px;margin-left:auto;font-size:36px}.checkout-page button{margin-left:auto;margin-top:50px}.checkout-page .test-warning{text-align:center;margin-top:40px;font-size:24px;color:red}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:1px solid darkgrey;padding:15px 0;font-size:20px;align-items:center}.checkout-item .image-container{width:23%;padding-right:15px}.checkout-item .image-container img{width:100%;height:100%}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:0 10px}.checkout-item .remove-button{padding-left:12px;cursor:pointer}

.header{display:flex;flex-direction:row;background-color:#fff;align-items:center;padding:0 30px;margin-bottom:0;position:-webkit-sticky;position:sticky;top:0px;z-index:100;width:100vw;margin-left:-60px;height:60px}.header__logo img{height:30px;position:relative;top:3px}.header__nav{display:flex;flex:1 1;list-style-type:none;justify-content:flex-end}.header__link a{margin-left:30px;text-decoration:none;color:black;font-size:15px}.header__icons{display:flex;list-style-type:none}.header__icons li{margin-left:20px}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-item{width:100%;display:flex;height:80px;margin-bottom:15px}.cart-item img{width:30%}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:10px 20px}.cart-item .item-details .name{font-size:16px}

.cart-dropdown{position:absolute;width:240px;height:340px;display:flex;flex-direction:column;padding:20px;border:1px solid black;background-color:white;top:90px;right:40px;z-index:5}.cart-dropdown .empty-message{font-size:18px;margin:50px auto}.cart-dropdown .cart-items{height:240px;display:flex;flex-direction:column;overflow:scroll}.cart-dropdown button{margin-top:auto}

.sign-in-and-sign-up{width:850px;display:flex;justify-content:space-between;margin:30px auto}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

.sign-in{width:30vw;display:flex;flex-direction:column}.sign-in .title{margin:10px 0}

.sign-up{display:flex;flex-direction:column;width:380px}.sign-up .title{margin:10px 0}

